.heading-block {
  position: relative;
  overflow: hidden;
  height: 530px;
  width: 100%;

  &__background {
    background-repeat: no-repeat;
    background-position: center;
    height: 100%;

    &::before {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      display: block;
      content: "";
      background: rgba(97, 65, 19, 0.45);
      width: 100%;
      height: 100%;
    }
  }

  &__image {
    width: 100%;
    object-fit: cover;
  }

  &__wrapper {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
  }
}

.heading-wrapper {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  text-align: center;
}

.heading {
  display: inline-block;
  border: 3px solid #e7e3dc;
  padding: 24px;
}