.about {
  background: #fff;
  color: #4e1720;

  &_pd {
    padding: 0 0 100px 0;
  }

  &__content {
    margin-bottom: 55px;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 1.8fr 1fr;
    column-gap: 50px;
  }

  &__note {
    position: relative;
    padding: 56px 0;
    z-index: 1;

    &::before {
      position: absolute;
      top: 15px;
      left: 15px;
      right: 15px;
      bottom: 15px;
      content: "";
      display: block;
      border: 1px solid #cea76f;
      z-index: -1;
    }

    &::after {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      display: block;
      content: "";
      z-index: -2;
      background: #f5f5f5;
    }
  }

  &__quote {
    padding: 0 50px;
    text-align: center;
  }

  &__text {
    &::before {
      content: "\00ab";
    }

    &::after {
      content: "\00bb";
    }
  }

  &__title {
    margin-top: 25px;
    font-weight: 700;
    text-transform: uppercase;
  }
}

.image {
  width: 100%
}