@import '../../style/variable';

.footer {
  min-height: 100%;

  &__link {
    padding: 6px 0;
    font-size: 0.9rem;
    font-weight: 600;
    text-decoration: none;
    color: $px-yellow;

    &:hover {
      color: $px-yellow;
    }
  }

  &__social {
    min-height: 100%;
    padding: 7px 0;
    background: $px-social;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 230px 1fr;
    column-gap: 5px;
    align-items: center;
  }

  &__contacts {
    display: grid;
    grid-template-columns: 2fr 1fr;
    justify-items: center;
    padding: 6px 0;
    margin: 6px 0;
  }

  &__information {
    background: $px-footer;
    padding: 40px 0 25px 0;
  }

  &__container {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-flow: row dense;
    column-gap: 10px;
    color: $px-yellow;
  }

  &__left {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 10px;
  }

  &__logo {
    max-width: 100%;
  }

  &__right {
    display: grid;
    grid-auto-rows: auto;
    row-gap: 20px;
  }

  &__bottom {
    margin-top: 40px;
    padding-top: 25px;
    border-top: 1px solid $px-yellow;
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 3;
    font-weight: 600;
  }
}

.value {
  padding-left: 10px;

  &:hover {
    text-decoration: underline;
  }
}